/**
 *  Find count number for clicked filter label
 *
 * @param {array} countEachResult
 * @param {string} radio
 */

const getCountForLabel = (countEachResult, radio) => {
  return countEachResult
    .filter(result => radio === result.key)
    .map(result => ` (${result.count})`);
};

export default getCountForLabel;
